@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.action{
  z-index: 1;
  position:'absolute'

}

.button-group{
  padding-bottom: 100px;
  margin: 0;
  position: absolute;
  left: 2%;
  bottom: 18%;
}

.create-group{
  padding-bottom: 100px;
  margin: 0;
  position: absolute;
  left: 2%;
  bottom: 2%;
}

#matrix-select {
  position: absolute;
  top: 17%;
  padding: 20px;
  z-index: 2;
}

#logo{
  padding-top: 10px;
  font-size: 2.5em;
  text-shadow: -1px -1px 0 rgb(0, 0, 0), 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  padding-left: 40px;
  color: #bbe1fa;
  position: absolute;
}

.App-link {
  color: #61dafb;
}


body {
  background-color: #0f4c75;
}

.Collapsible {
  font-family: "Roboto", sans-serif;
  background-color: #1b262c;
  color: white;
  border: solid 8px #1b262c;
  border-radius: 4px;
  padding: 10px;
  margin: 20px;
  margin-bottom: 0;
  cursor: pointer;
  text-align: center;
}

.input-level {
  background-color: #bbe1fa;
  color: black;
  margin: 10px;
  margin-left: 20px;
  padding-bottom: 20px;
  border-radius: 7px;
}

label {
  margin: 10px;
}

input {
  margin: 5px;
  padding: 3px;
  border-radius: 7px;
  background-color: white;
  border: 2px solid black;
  text-align: center;
}

button {
  padding: 6px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 20px;
  border: solid 3px #0f4c75;
  color: white;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

button:hover {
  background-color: #00aacc;
  color: #ffffff;
}

button:disabled,
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}

.instructions {
  text-decoration: none;
}

.levels {
  background-color: #1b262c;
  margin: 20px;
  margin-top: 0px;
  padding: 10px;
  border: solid 8px #1b262c;
  border-radius: 5px;
  color: white;
  text-align: center;
}

.level {
  background-color: #3282b8;

  padding: 10px;
}

.ins-list {
  width: 60%;
  margin: auto;
}

